@use 'base/variables' as v;
@use 'base/mixins' as m;

.proyects{
  h2{
    padding-bottom: 3rem;
  }

  .title-description{
    font-size: 1.6rem;
    text-align: center;
    margin-bottom: 2rem;
  }

  .button-all-proyects{
    @include m.button(v.$principalColor, v.$white);
  }

  .proyects-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
    row-gap: 5rem;

    @include m.tablet{grid-template-columns: repeat(2, 50%)};
    @include m.phone{grid-template-columns: 1fr};

    .proyect{
      background-color: rgba($color: #ffffff, $alpha: .08);
      border-radius: 1rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 40% 60%;
      gap: 3rem;
      -webkit-box-shadow: 0px 10px 16px -1px rgba(0,0,0,0.75);
      -moz-box-shadow: 0px 10px 16px -1px rgba(0,0,0,0.75);
      box-shadow: 0px 10px 16px -1px rgba(0,0,0,0.75);


      .img-container{
        height: 100%;
        overflow: hidden;

        img{
          width: 100%;
          object-fit: cover;
          object-position: center center;
          border-radius: 1rem;
          transition: transform .3s ease;
        }
      }

      .text-container{
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        justify-content: space-between;
        align-items: center;

        p{
          font-size: 2rem;
          text-align: center;
        }

        .visit-site-button{
          @include m.button(v.$principalColor, v.$white);
        }
      }
      
      &:hover{

        .img-container{
          img{
            transform: scale(1.2) rotate(5deg);
          }
        }
      }
    }
  }
}

.proyects-page{
  padding-top: 15rem;
}