body {
  min-height: 100%;
  color: #FFFFFF;
  font-family: "Open Sans", sans-serif;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  width: 85%;
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  margin: 30rem auto;
}
@media (max-width: 768px) {
  .section {
    margin: 10rem auto;
  }
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

ul, li {
  list-style: none;
}

img {
  max-width: 100%;
  width: inherit;
  height: inherit;
