  }
}

.activeLink {
  color: #537FE7 !important;
}

.disclaimer {
  opacity: 0;
}

.separator {
  width: 100%;
  margin: 4rem auto;
  border: none;
  border-radius: 1rem;
  height: 0.3rem;
  outline: none;
  background: -webkit-linear-gradient(to right, #537FE7, #C0EEF2);
  background: linear-gradient(to right, #537FE7, #C0EEF2);
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  font-weight: 400;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
  background: -webkit-linear-gradient(to right, #537FE7, #C0EEF2);
  background: linear-gradient(to right, #537FE7, #C0EEF2);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

h1 {
  font-size: 4.5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  color: #e1e1e1;
  font-size: 2.6rem;
  font-weight: 300;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  z-index: 1;
  transition: all 0.3s ease;
}
.header .logo-container .logo {
  width: 25rem;
  height: 10rem;
}
.header .logo-container .logo rect {
  stroke: #FFFFFF;
  transition: stroke 0.2s ease;
}
@media (max-width: 480px) {
  .header .logo-container .logo {
    width: 20rem;
    height: 10rem;
  }
}
.header .logo-container .logo .logoPath {
  fill: #FFFFFF;
}
.header .menu-icon {
  font-size: 4rem;
  display: none;
  z-index: 20;
}
@media (max-width: 768px) {
  .header .menu-icon {
    display: inline-block;
  }
}

.down {
  background-color: #191b18;
  padding: 0rem 2rem;
  -webkit-box-shadow: 0px 0px 38px -24px rgb(115, 115, 115);
  -moz-box-shadow: 0px 0px 38px -24px rgb(115, 115, 115);
  box-shadow: 0px 0px 38px -24px rgb(115, 115, 115);
}

.social-links .links-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.social-links .social-icon {
  align-items: center;
  color: #e1e1e1;
  display: flex;
  height: 70px;
  justify-content: center;
  margin: 2px;
  text-decoration: none;
  transition: all 0.2s ease;
  width: 70px;
  font-size: 3rem;
}
.social-links .round {
  border-radius: 50%;
}
.social-links .social-icon:hover {
  color: #fff;
}
.social-links .shadow:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.social-links #linkedin:hover {
  background-color: #0177B5;
}
.social-links #instagram:hover {
  background: #d6249f;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
}
.social-links .icono-social:active {
  transform: scale(0.9);
}

.cards .title-cards-container {
  text-align: center;
  margin-bottom: 3rem;
}
.cards .paragraph-cards-container {
  text-align: center;
  margin-bottom: 3rem;
}
.cards .cards-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem;
}
@media (max-width: 768px) {
  .cards .cards-container {
    grid-template-columns: 1fr;
  }
}

.card {
  padding: 2rem;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(0.4rem);
  border-radius: 1rem;
  -webkit-box-shadow: 0px 10px 16px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 16px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 16px -1px rgba(0, 0, 0, 0.75);
}
.card .card-icon {
  font-size: 10rem;
}
.card .card-title {
  margin: 2rem 0;
  font-size: 3rem;
}
.card .separator {
  margin: 2rem auto;
}
.card .technologies {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.card .technologies .skill-icon {
  color: #537FE7;
  width: 5rem;
  height: 5rem;
}
.card .technologies .skill-icon.fa-html5 {
  color: #D94A1E;
}
.card .technologies .skill-icon.fa-css3-alt {
  color: #0B6DAF;
}
.card .technologies .skill-icon.fa-sass {
  color: #C36291;
}
.card .technologies .skill-icon.fa-js-square {
  color: #D99A26;
}
.card .technologies .skill-icon.fa-gulp {
  color: #DC484C;
}
.card .technologies .skill-icon.fa-php {
  color: #404D85;
}
.card .technologies .skill-icon.fa-database {
  color: #DC931F;
}
.card .technologies .skill-icon.fa-code-branch {
  color: #E74B27;
}
.card .technologies .skill-icon.fa-clipboard-list {
  color: #000000;
}
.card .skills-list {
  margin: 4rem 0;
}
.card .skills-list .skill {
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;
}
.card .skills-list .skill .skill-icon {
  flex: 1;
  font-size: 4rem;
}
.card .skills-list .skill .skill-text {
  flex: 3;
  font-size: 2rem;
  text-align: start;
}

.lds-facebook {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #fff;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}

.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}

.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}

.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}

@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
.loader {
  background-color: #537FE7;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  clip-path: circle(150% at 100% 0);
  transition: clip-path 1s ease-in-out;
}

.loader2 {
  clip-path: circle(0% at 100% 0);
}

body::-webkit-scrollbar {
  width: 1rem;
  background: #b8b8b8;
}

body::-webkit-scrollbar-thumb {
