  background-color: #537FE7;
  padding: 1rem 3rem;
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.2s;
  text-align: center;
  margin: 1rem 0rem;
  border-radius: 2.2rem;
}
@media (max-width: 768px) {
  .proyects .button-all-proyects {
